body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: linear-gradient(
    to left,
    rgb(213, 255, 225) 0%,
    rgb(255, 160, 247) 100%
  );
}
.App {
  text-align: center;
}

.App img {
  width: 80px;
  display:block;
  margin:.5em auto;
}
.card {
    margin-bottom:.5em;
    border:1px solid #ccc;
    border-radius:5px;
    padding:1em;
    background-color: #fff;
    transition:all .2s;
}

.card h4 {
    margin:0;
}

.card:hover {
    cursor: pointer;
    box-shadow: 0px 0px 5px rgba(0,0,0,.3);
}
.card-list {
    display:grid;
    grid-template-columns: repeat(4,1fr);
    grid-gap:.3em;
    width: 80vw;
    margin:0 auto;
}

@media (max-width: 600px) {
    .card-list {
        grid-template-columns: 1fr 1fr;
        width: 90%;
    }
}
.search {
    -webkit-appearance: none;
    outline: none;
    border:1px solid #ccc;
    margin: 1em 0;
    font-size:1.5rem;
    width: 50vw;
    border-radius: 50px;
    padding:15px;
}

@media (max-width: 600px) {
    .search {
        width: 90%;
    }
}
